<template>
	<div>
		<!-- wrap -->
		<div id="wrap">
			<!-- header -->
			<header id="sub">
				<div class="header_wrap">
					<h1>서식관리</h1>
					<a @click="formatRegFlag=true"><div class="reg_btn"><div class="reg_btn_txt">폴더 등록</div></div></a>
				</div>
			</header>
			<!-- header -->
			<div class="txt_area_wrap">
				<div class="txt_area">총 <b>{{getFormatTypeList.length}}</b>개의 폴더가 있습니다.</div>
			</div>
			<!-- content -->
			<div id="contents_document" v-for="(formType,index) in getFormatTypeList" :key="index">
				<!-- 전체 -->
				<div class="document_list" @click="moveFolderIn(formType)">
					<p class="style01">
						<span>{{formType.formTypeTitle}}</span>
					</p>
					<p class="style02">
						<span style="color:#666;">장례박사 <span class="flato" style="padding-left:6px;">{{YYYYMMDDDate(formType.regDate)}}</span></span>
					</p>
				</div>
			</div>

			
			<div  style="height:65px;width:100%">
			</div>
			<!-- //content -->
			<!-- footer -->
			<Footer/>
			<!-- //footer -->
		</div>
		<FormFolderAdd v-if="formatRegFlag" :closePopup="closePopup"/>
	</div>
</template>

<script>
import Footer from '@/components/common/Footer'

import FormFolderAdd from '@/components/format/FormFolderAdd'
import myMixin from '@/mixin/mixin'

import { mapGetters } from 'vuex'

export default {
	name: 'Faq',
	mixins: [myMixin],
	components: {
		Footer,
		FormFolderAdd
	},
	props: {},
	data() {
		return {
			formatRegFlag:false
		}
	},
	computed: {
		...mapGetters(['getFormatTypeList', 'getFormatTypeRightFlag']),
	},
	mounted() {
		this.$store.dispatch('REQUEST_FORMAT_FOLDER_LIST')
	},
	beforeDestroy() {
		this.$store.commit('FORMAT_INIT')
	},
	methods: {
		moveFolderIn(formatType) {
			this.$router.push({
				path: '/formatIn',
				query: {
					id: formatType.id,
				}
			})
		},
		closePopup(){
			this.formatRegFlag=false
		}
	}
}
</script>

